import Vue from "vue"
import Vuex from "vuex"
import * as Tone from "tone";

//
const synth = new Tone.Synth().toDestination();

Vue.use(Vuex);


let state={
	version:'0.1.0',
	systemInfo:{},
	defaultHeight:{
		//
		"min-height":"800rpx",
		//
		"height":"800rpx"
	},
	mood:[
		['Happy','Sad','Annoyed','Strong','Anxous','Content','Excited'],
		['Scared','Peaceful','Loved','Vulnerable','Regret','Overwhelmed','Hight'],
		['Upset','Mature','Weak','Alone','Clouded','Lost','Grateful'],
		['Low','Confused','Guilty','Depressed','Angry','Lonely']
		],
	// project
	list:{
		mood:[]
	},
	// list
	menu:[
		{
			id: 'AA', 				//id
			title:'title', 			//
			backgroundMusic:'aaa', //
			dateTime:'',			//
			thoughts:'............',	//
			location:'',	//
			imageUrl:'',			//
			mood:[],				//
			music:[]				//
		}
	],
	
	//
	playing:false
}

let mutations={
	addMood(state,value){
		state.list.mood.push(value);
	},
	deleteMood(state,value){
		state.list.mood.splice(value,1);
	},
	setSystemInfo(state,value){
		state.systemInfo = value;
		//
		state.defaultHeight['height'] = (value.windowHeight - 43)+"px";
	},
	PLAYER_START(state,value){
		state.playing = true;
	},
	PLAYER_STOP(state,value){
		state.playing = false;
	},
}

let getters={
	getMood(state){
		return state.mood;
	},
	getWindowsHeight:(state)=>{
		return state.systemInfo.windowHeight - 43
	},
	findMood:(state)=>(value)=>{
		return state.list.mood.findIndex(item=>{
					return item === value
				});
	},
	defaultHeight(state){
		return state.defaultHeight
	}
}

let actions={
	//
	initPlayer({commit,state}){

	},
	setPlayer({commit,state},value){
		//
		if(!state.playing) state.player = new Tone.Player(value).toDestination();
	},
	//
	playerStart({commit,state}){
		if(!state.playing){
			Tone.loaded().then(() => {
				state.player.start();
				commit('PLAYER_START');
			});
		}
	},
	playerStop({commit,state}){
		if(state.playing){
			state.player.stop();
			commit('PLAYER_STOP');
		}
		else{
			console.log('')
		}
	},
	//
	synthStart({commit}){
		const now = Tone.now();
		synth.triggerAttackRelease("C4", "8n", now);
		synth.triggerAttackRelease("E4", "8n", now + 0.5);
		synth.triggerAttackRelease("G4", "8n", now + 1);
	}
}

const store = new Vuex.Store({
		state,
		mutations,
		getters,
		actions
	})

export default store;